.wp-block-gb-block-gb-button {
	width: 100%;
	height: 100%;

	&:not( .alignwide ):not( .alignfull ):not( .alignleft ):not( .alignright ):not( .wp-block-separator ):not( .woocommerce ) {
		.entry-content & {
			max-width: 1140px;
			margin: 0 auto;
		}
		&.gb-button-align-right {
			text-align: right;
		}

		&.gb-button-align-center {
			text-align: center;
		}

		&.gb-button-align-left {
			text-align: left;
		}
	}

	&__wrapper {
		width: 50%;

		@media ( max-width: 768px ) {
			width: 100%;
			padding: 0 10px 0 10px;
		}
	}

	&__btn {
		display: inline-block;
		height: 60px;
		border: none;
		border-radius: 4px;
		background: linear-gradient( 135.46deg, #45c7d2 0%, #3199a2 100% );
		box-shadow: 0 0 30px -8px #3eb9c3;
		color: #fff;
		line-height: 60px;
		text-align: center;
		font-weight: normal;
		cursor: pointer;
		transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out,
			border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;

		a {
			font-size: 24px;
			padding: 14px 72px;
			color: #fff;
			text-decoration: none;
		}
	}

	&__btn:hover {
		background: linear-gradient(
			135deg,
			#3199a2 0%,
			#3199a2 9.23%,
			#45c7d2 100%
		);
		box-shadow: 0 0 30px -8px #3eb9c3;
		color: #fff;
	}

	&__btn-text {
		font-size: 24px;
		color: #fff;
	}
}

.full-width {
	width: 100%;
	padding: 0;
}

.wp-block-gb-block-gb-button {
	width: 100%;
	height: 100%;

	&:not( .alignwide ):not( .alignfull ):not( .alignleft ):not( .alignright ):not( .wp-block-separator ):not( .woocommerce ) {
		> .wp-block-gb-block-gb-button__wrapper
			> .wp-block-gb-block-gb-button__btn
			> a:focus {
			background: none;
			text-decoration: none;
		}
	}
}
